.spinner--start div {
  box-sizing: border-box;
  width: 80px;
  height: 80px;
  border: 10px solid rgba(0, 0, 0, 0);
  border-top-color: #ff980d;
  border-radius: 50%;
  margin: 8px;
  animation: spinner 1.3s cubic-bezier(.4, 0, .5, 1) infinite;
  display: block;
  position: absolute;
}

.spinner--start div:nth-child(1) {
  animation-delay: -450ms;
}

.spinner--start div:nth-child(2) {
  animation-delay: -300ms;
}

.spinner--start div:nth-child(3) {
  animation-delay: -150ms;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.upward {
  width: 50px;
  height: 50px;
  cursor: pointer;
  opacity: 0;
  z-index: 999;
  background-color: #ff980d;
  border: 3px solid #ff0303;
  border-radius: 50%;
  font-size: 20px;
  font-weight: 800;
  line-height: 70px;
  transition: all .5s;
  position: fixed;
  bottom: 700px;
  right: 30px;
}

.active {
  visibility: visible;
  opacity: 1;
  z-index: 999;
  bottom: 40px;
}

.upward:hover {
  background-color: #f50;
  scale: 1.2;
}

.arrow {
  fill: #ff0303;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

